.formContainer {
    width: 50%;
    margin: 50px auto;
    background-color: white;
    text-align: center;
    padding: 10px;
}
.formContainer > h1 {
    letter-spacing: 2px;
    word-spacing: 5px;
    text-transform: uppercase;
}
.formContainer > form {
    background-color: yellow;
    padding: 10px;
    box-shadow: 0 0 5px gold;
}
.inp {
    border: 0;
    outline: 0;
    padding: 5px;
    font-size: 18px;
    margin: 5px auto;
    color: rgba(0, 0, 0, 0.671);
    letter-spacing: 2px;
    width: 70%;
}
.inp::placeholder {
    letter-spacing: 2px;
    font-size: 15px;
}
.button {
    box-shadow: 0 0 5px gold;
    border: 0;
    border-radius: 15px;
    font-size: 18px;
    padding: 5px 10px;
    letter-spacing: 2px;
}
.button:hover {
    box-shadow: 0 0 5px rgb(0, 195, 255);
    background-color: lightblue;
    font-size: 20px;
}
