.ProductDiv {
    display: flex;
    flex-wrap: wrap;
}

.tableContainer {
    width: 70%;
    background-color: grey;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
}
.table {
    background-color: white;
    margin: 50px  auto 0px auto;
    padding: 10px;
}
.btnCont {
 padding: 5px;
}
.btnCont > button {
    padding: 5px;
    border: 0;
    margin: 0 10px;
    letter-spacing: 2px;
    text-transform: uppercase;
    border-radius: 10px;
    background-color: lightblue;
}
.btnCont > button:hover {
    background-color: gold;
}

.row {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.315);
}
.row > td {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.315);
    padding: 5px 10px;
    font-size: 20px;
    letter-spacing: 2px;
    text-transform: capitalize;
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.342);
    font-weight: 500;
}

.chkCont {
    background-color: coral;
    width: 30%;
}

.chkcontSub {
    background-color: white;
    width: 70%;
    margin: 55px auto;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    padding: 10px 0px;
    box-shadow: 0 0 5px black;
}
.chkcontSub > h4 {
    margin: 0;
    padding: 2px;
    letter-spacing: 2px;
}