.newsCard {
    width: 30%;
    background-color: white;
    margin: 10px auto;
    padding: 10px;
    box-shadow: 0 0 5px black;
    border-radius: 20px 0 0 20px ;
    height: 500px;
    overflow-y: auto;
}
.newsCard:hover {
    box-shadow: 0 0 5px gold;
}
.newsCard > div {
    width: 100%;
}
.newsCard > h3,h4 {
    letter-spacing: 1px;
}

.newsCard > p {
    letter-spacing: 2px;
}

.read {
    border: 0;
    padding: 2px;
    background-color: white;
    font-size: 15px;
    letter-spacing: 2px;
}
.read:hover {
    background-color: lightblue;
}